@charset "UTF-8";
:root {
  --fontMain: Rubik, sans-serif;
  --fontSize: 16px;
  --lineHeight: 1.5;
  --brand: #4974c2;
  --brandLight: #6f91cf;
  --voice: #4974c2;
  --voiceLightest: #e9f6fd;
  --success: #55b43c;
  --successLight: #d1ebcb;
  --danger: #db4815;
  --intense: #090914;
  --major: #323239;
  --minor: #6c6c6c;
  --muted: #92929a;
  --mutedLight: #d7d7d7;
  --mutedLightest: #f5f5f5;
  --disabled: #d7d7d7;
  --inverse: #fff;
  --bg: #fff;
  --bgAlter: #ebebef;
  --border: #c4c4cd;
  --containerMax: 1150px;
  --sidePadding: 16px;
  --gap: 30px;
}
@media (min-width: 576px) {
  :root {
    --sidePadding: 20px;
  }
}
@media (min-width: 768px) {
  :root {
    --sidePadding: 20px;
  }
}

[data-theme=dark] {
  --brand: #4974c2;
  --voice: #6f91cf;
  --voiceLightest: #e9f6fd;
  --success: #55b43c;
  --successLight: #d1ebcb;
  --danger: #eb3a3f;
  --intense: #e9ebf2;
  --major: #e9ebf2;
  --minor: #f5f5f5;
  --muted: #6c6c6c;
  --mutedLight: #6c6c6c;
  --mutedLightest: #393c44;
  --disabled: #d7d7d7;
  --inverse: #323239;
  --bg: #22252e;
  --bgAlter: #555559;
  --border: #555559;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

html {
  text-size-adjust: 100%;
  text-rendering: optimizelegibility;
  -webkit-font-smoothing: subpixel-antialiased;
  cursor: default;
}

body {
  margin: 0;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

img {
  max-width: 100%;
  vertical-align: middle;
}

svg {
  display: inline-block;
}

picture,
picture img {
  display: block;
}

input,
textarea,
button {
  font-size: inherit;
  font-family: inherit;
}

input:focus,
textarea:focus,
button:focus {
  outline: none;
}

html {
  font-size: 16px;
}

body {
  color: var(--major);
  font-family: var(--fontMain);
  font-size: var(--fontSize);
  line-height: var(--lineHeight);
}

a {
  color: inherit;
  text-decoration: none;
  color: var(--voice);
  border-bottom: 1px solid currentColor;
  text-decoration: underline;
  text-underline-position: under;
  transition: color 0.12s, border-color 0.12s, background-color 0.12s, box-shadow 0.12s;
}
a:hover {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

p {
  max-width: 750px;
  margin-top: 1rem;
  margin-bottom: 1rem;
}
p:first-child {
  margin-top: 0px;
}
p:last-child {
  margin-bottom: 0px;
}
@media (min-width: 768px) {
  p {
    margin-top: 1rem;
    margin-bottom: 1rem;
  }
}

h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6 {
  margin-top: 2.2rem;
  margin-bottom: 1rem;
  color: var(--intense);
  line-height: 1.2;
  font-weight: bold;
}
h1:first-child, h2:first-child, h3:first-child, h4:first-child, h5:first-child, h6:first-child,
.h1:first-child, .h2:first-child, .h3:first-child, .h4:first-child, .h5:first-child, .h6:first-child {
  margin-top: 0px;
}
h1:last-child, h2:last-child, h3:last-child, h4:last-child, h5:last-child, h6:last-child,
.h1:last-child, .h2:last-child, .h3:last-child, .h4:last-child, .h5:last-child, .h6:last-child {
  margin-bottom: 0px;
}
@media (min-width: 768px) {
  h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6 {
    margin-top: 2.2rem;
    margin-bottom: 1rem;
  }
}

h1, .h1 {
  font-size: 24px;
  line-height: 1.2;
}
@media (min-width: 768px) {
  h1, .h1 {
    font-size: 32px;
    line-height: 1.05;
  }
}

h2, .h2 {
  font-size: 22px;
}
@media (min-width: 768px) {
  h2, .h2 {
    font-size: 30px;
  }
}

h3, .h3 {
  font-size: 20px;
}
@media (min-width: 768px) {
  h3, .h3 {
    font-size: 24px;
  }
}

h4, .h4 {
  font-size: 20px;
}

h5, .h5 {
  color: var(--muted);
  font-size: 18px;
}

h6, .h6 {
  font-size: 16px;
}

code {
  font-family: monospace;
  border-radius: 3px;
  color: #eb5a46;
  padding: 2px 4px;
  margin: -1px 2px;
  font-size: 14px;
  color: #e83e8c;
  color: #df1e65;
  word-wrap: break-word;
  background-color: #f0f0f4;
  background-color: #eae8ef;
}

pre {
  -webkit-overflow-scrolling: touch;
  transform: translateZ(0);
  background-color: #f0f0f4;
  border-radius: 3px;
  margin: 12px 0;
  padding: 10px 12px;
  word-break: normal;
  word-wrap: normal;
  overflow-wrap: normal;
  tab-size: 4;
  font-size: 14px;
  line-height: 1.5;
}
pre:first-child {
  margin-top: 0px;
}
pre:last-child {
  margin-bottom: 0px;
}

pre code {
  background-color: none;
  border-radius: 0;
  box-shadow: none;
  color: #172b4d;
  margin: 0;
  padding: 0;
  color: #e83e8c;
}

/* code, kbd, pre, samp */
ul {
  margin-top: 1rem;
  margin-bottom: 1rem;
  padding-left: 0;
  list-style-type: none;
}
ul:first-child {
  margin-top: 0px;
}
ul:last-child {
  margin-bottom: 0px;
}

ul > li {
  max-width: 778px;
  position: relative;
  margin-top: 0.5rem;
  margin-left: 0;
  padding-left: 1rem;
}
@media (min-width: 768px) {
  ul > li {
    padding-left: 1.175rem;
  }
}

ul > li:before {
  width: 6px;
  height: 6px;
  content: "";
  display: block;
  position: absolute;
  top: 0.55rem;
  left: 2px;
  background-color: var(--major);
  border-radius: 8px;
}

ul > li:first-child {
  margin-top: 0;
}

ul > li > ul:first-child {
  margin-top: 5px;
}

ul ul {
  margin-top: 5px;
}

blockquote {
  border-left: 6px solid #4974c2;
  padding-top: 2px;
  padding-bottom: 2px;
  padding-left: 20px;
  margin-left: 0;
  margin-right: 0;
  color: var(--intense);
  font-weight: 500;
}
@media (min-width: 768px) {
  blockquote {
    max-width: 750px;
    padding-top: 8px;
    padding-bottom: 8px;
    padding-left: 30px;
  }
}

sup {
  font-size: 0.6em;
  vertical-align: 0;
  position: relative;
  top: -0.5em;
}

:root {
  --boxPadding: 20px;
  --boxPadding--sm: 15px;
}
@media (--up-sm) {
  :root {
    --boxPadding: 25px;
    --boxPadding--sm: 15px;
  }
}
@media (--up-md) {
  :root {
    --boxPadding: 30px;
    --boxPadding--sm: 20px ;
  }
}

.Box {
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
  padding: var(--boxPadding);
  background-color: var(--bg);
  border: 1px solid var(--border);
  border-radius: 0.5rem;
  padding: 1rem;
}
.Box:first-child {
  margin-top: 0px;
}
.Box:last-child {
  margin-bottom: 0px;
}
.Box:first-child {
  margin-top: 0;
}
.Box:last-child {
  margin-bottom: 0;
}
@media (--up-md) {
  .Box {
    margin-top: 35px;
    margin-bottom: 35px;
  }
}

.Box.-shadowIntense {
  box-shadow: 0px 12px 24px rgba(0, 0, 0, 0.12);
}

.Box.-brandLight {
  background-color: rgba(202, 158, 40, 0.1);
  border-width: 0;
}

[data-theme=dark] .Box.-brandLight {
  background-color: rgba(255, 255, 255, 0.1);
}

.Box.-grayLight {
  background-color: var(--mutedLightest);
  border-width: 0;
}

[data-theme=dark] .Box.-grayLight {
  background-color: rgba(255, 255, 255, 0.1);
}

.Box.-noFrame {
  border: 0;
  padding: 0;
  background-color: transparent;
}

.Box.-sm {
  padding: var(--boxPadding--sm);
}

.Box.-lg {
  padding: 40px;
}

.Box.-beyond {
  border-radius: 0;
  border-left: 0;
  border-right: 0;
  margin-left: calc(var(--sidePadding) * -1);
  margin-right: calc(var(--sidePadding) * -1);
}

@media (max-width: 767.98px) {
  .Box.-sm-beyond {
    border-radius: 0;
    border-left: 0;
    border-right: 0;
    margin-left: calc(var(--sidePadding) * -1);
    margin-right: calc(var(--sidePadding) * -1);
  }
}

