.contextHidden {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s ease 0.6s, opacity 0.6s ease, transform 0.6s ease;
}

.is-open .contextHidden {
  visibility: visible;
  opacity: 1;
  transition: opacity 0.6s ease, transform 0.6s ease;
}

.appear-slideInRight,
.appear-slideInDown,
.appear-fadeIn {
  --appearDuration: .6s;
  --appearDelay: .2s;
  --fromX: 1rem;
  animation-delay: var(--appearDelay);
  animation-fill-mode: both;
  animation-duration: var(--appearDuration);
  animation-timing-function: ease-in-out;
}

.appear-slideInRight {
  --fromX: -10px;
  animation-name: appearSlideInRight;
}

.appear-slideInDown {
  --fromX: -10px;
  animation-name: appearSlideInDown;
}

.appear-fadeIn {
  animation-name: appearFadeIn;
}

@keyframes appearSlideInRight {
  from {
    opacity: 0;
    transform: translateX(var(--fromX, -1rem));
  }
  to {
    opacity: 1;
    transform: translateX(0px);
  }
}
@keyframes appearSlideInDown {
  from {
    opacity: 0;
    transform: translateY(var(--fromX, -1rem));
  }
  to {
    opacity: 1;
    transform: translateY(0px);
  }
}
@keyframes appearFadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
